.signupForm {
  background-color: beige;
  color: maroon;
  box-shadow: 8px 8px 8px 8px rgba(0, 0, 0, 0.75);
  width: 300px;
  padding: 50px;
  margin: auto;
  margin-top: 13%;
  border-radius: 30px;
}

label {
  padding: 20px 0px;
}

.form-group {
  padding: 10px;
}

.signupForm:hover {
  /* opacity: 0.8; */
  transform: scale(1.2);
  color: maroon;
}

.signupForm button {
  background-color: maroon;
  color: beige;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.signupForm button:hover {
  opacity: 0.5;
}

input {
  width: 96%;
  padding: 8px;
}
