.card {
    box-shadow: 6px 6px 6px 6px rgba(37, 26, 26, 0.2);
    max-width: 300px;
    display: inline-block;
    text-align: center;
    font-family: arial;
    background-color:beige;
    flex-direction:column;
    border-radius: 30px;
    padding: 10px 40px;
    margin-top: 40px;
    margin-left: 20px; 
  }
  
  .card:hover  {
    transform: scale(1.05);
    color: maroon;
    font-size: medium;
  }
  
    
  .profile-icon{
    width: 150px;
    margin: auto;
    margin-top: 20%;
    text-align: center;
    font-family: arial;
    padding: 20px;
    background-color: rgb(254, 254, 254);
    border-style: solid;
    border-color: black;
  }
    
  #cardbutton {
    border: solid black;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: maroon;
    text-align: center;
    cursor: pointer;
    width: 40%;
    font-size: 18px;
    margin-bottom: 10px;
    }
    
  button:hover {
    opacity: 0.5;
  }
