.calculator {
  /* margin: 30px; */
  background-color: beige;
  width: 370px;
  margin: auto;
  margin-top: 15px;
  border-radius: 30px;
  /* color: rgb(25, 17, 7); */
  /* border: 1px solid #041d01; */
  padding: 20px;
  box-shadow: 8px 8px 8px 8px rgba(0, 0, 0, 0.75);
}

